/* Mobile view, not display */
.timeline {
    display: none;
}

@media only screen and (min-width: 800px) {
    .timeline {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        padding-top: 50px;
    }
    .timeline li {
        position: relative;
        height: 50px;
    }
    .timeline li::after {
        /* this is used to create the table horizontal lines */
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 1px;
        background: #eaeaea;
    }
    .timeline li:last-of-type::after {
        display: none;
    }
    .timeline li span {
        display: none;
    }
}

@media only screen and (min-width: 1000px) {
    .timeline li::after {
        width: calc(100% - 60px);
        left: 60px;
    }
    .timeline li span {
        display: inline-block;
        -webkit-transform: translateY(-50%);
        transform: translateY(-50%);
    }
    .timeline li:nth-of-type(2n) span {
        display: none;
    }
}

.single-event {
    /* force them to stay on one line */
    flex-shrink: 0;
    float: left;
    height: 150px;
    width: 70%;
    max-width: 300px;
    box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.2);
    margin-right: 20px;
    -webkit-transition: opacity 0.2s, background 0.2s;
    transition: opacity 0.2s, background 0.2s;
}

.single-event:last-of-type {
    margin-right: 5%;
}

.single-event a {
    display: block;
    height: 100%;
    padding: 0.8em;
}

.single-event a:hover {
    background-color: rgba(255, 255, 255, 0.1);
}

@media only screen and (min-width: 550px) {
    .single-event {
        width: 40%;
    }
}

@media only screen and (min-width: 800px) {
    .single-event {
        position: absolute;
        z-index: 3;
        /* top position and height will be set using js */
        width: calc(100% + 2px);
        left: -1px;
        box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1),
            inset 0 -3px 0 rgba(0, 0, 0, 0.2);
        /* reset style */
        flex-shrink: 1;
        height: auto;
        max-width: none;
        margin-right: 0;
    }
    .single-event a {
        padding: 1.2em;
    }
    .single-event:last-of-type {
        /* reset style */
        margin-right: 0;
    }
    .single-event.selected-event {
        /* the .selected-event class is added when an user select the event */
        visibility: hidden;
    }
}

/* event name and date */
