    flex: 0 0 124px;
}

.buttons-bar {
    width: 100%;
    /* fixed height, no grow nor shrink */
    flex: 0 0 52px;
    padding-right: 10px;
    display: flex;
    /* flex-direction: row-reverse; */
    justify-content: flex-end;
    align-items: center;
}

.event-name,
.event-date,
.event-location {
    display: block;
    color: white;
    font-weight: bold;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.event-date {
    /* they are not included in the the HTML but added using JavScript */
    font-size: 1.4rem;
    opacity: 0.7;
    line-height: 1.2;
    margin-bottom: 0.2em;
}

.event-location {
    color: black;
    font-size: 1.6rem;
    opacity: 0.7;
    /* line-height: 1.2; */
    margin-bottom: 0.4em;
}

.event-description {
    flex-grow: 1;
    overflow: auto;
}

.close {
    /* this is the 'X' icon */
    position: absolute;
    top: 0;
    right: 0;
    z-index: 3;
    background: rgba(0, 0, 0, 0.1);
    /* replace text with icon */
    color: transparent;
    white-space: nowrap;
    text-indent: 100%;
    height: 80px;
    width: 80px;
}

.event-form.edit .close {
    height: 90px;
    width: 90px;
}

.close::before,
.close::after {
    /* these are the two lines of the 'X' icon */
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    width: 2px;
    height: 44px;
    background: white;
}

.close::before {
    -webkit-transform: translateX(-50%) translateY(-50%) rotate(45deg);
    transform: translateX(-50%) translateY(-50%) rotate(45deg);
}

.close::after {
    -webkit-transform: translateX(-50%) translateY(-50%) rotate(-45deg);
    transform: translateX(-50%) translateY(-50%) rotate(-45deg);
}

/* desktop styles */
@media only screen and (min-width: 800px) {
    .event-form {
        flex-direction: row;
    }

    /* header */
    .header {
        display: block;
        height: 100%;
        width: 28%;
    }
    .event-form.edit .header {
        height: 100%;
    }
    .header .content {
        /* reset style */
        display: block;
        padding: 0.8em;
    }

    /* body */
    .body {
        flex-grow: 1;
        height: 100%;
    }
    .event-info {
        padding: 2em 3em 0em 2em;
    }

    .event-name {
        font-size: 2rem;
    }

    /* smaller close icon on desktop */
    .close {
        width: 40px;
        height: 40px;
        background: transparent;
    }
    .event-form.edit .close {
        width: 40px;
        height: 40px;
    }
    .close:hover {
        background: rgba(0, 0, 0, 0.1);
    }
    .close::after,
    .close::before {
        background: #222222;
        height: 16px;
    }
}

@media only screen and (min-width: 1000px) {
    .header {
        width: 40%;
    }
    .header .content {
        padding: 1.2em;
    }
}

.event-modal {
    position: fixed;
    z-index: 10;
    top: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    visibility: hidden;
    /* Force Hardware acceleration */
    /* -webkit-transform: translateZ(0);
    transform: translateZ(0);
    -webkit-transform: translateX(100%);
    -ms-transform: translateX(100%); */

    /* -webkit-transition: visibility 0.4s, -webkit-transform 0.4s;
    transition: visibility 0.4s, -webkit-transform 0.4s; */
    -webkit-transition: visibility 0.3s, opacity 0.3s;
    transition: visibility 0.3s, opacity 0.3s;
    /* transition: transform 0.4s, visibility 0.4s, -webkit-transform 0.4s; */
}

.event-modal.modal-is-visible {
    /* .modal-is-open class is added as soon as an event is selected */
    /* -webkit-transform: translateX(0);
    -ms-transform: translateX(0); */
    opacity: 1;
    visibility: visible;
}

.cover-layer {
    /* layer between the content and the modal window */
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, 0.8);
    /* opacity: 0;
    visibility: hidden; */
    /* -webkit-transition: opacity 0.4s, visibility 0.4s; */
    /* transition: opacity 0.1s, visibility 0.1s; */
}

/* event form container style on mobile */
.event-form_container {
    position: relative;
    z-index: 2;
    width: 100%;
    height: 100%;
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    -webkit-transition: -webkit-transform 0.3s;
    transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
}

.modal-is-visible .event-form_container {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

@media only screen and (min-width: 800px) {
    /* event form container style on desktop */
    .event-form_container {
        width: 90%;
        max-width: 800px;
        height: 90%;
        max-height: 480px;
        top: 50%; /* vertical center the event form div */
        -webkit-transform: translateY(-65%);
                transform: translateY(-65%);
        margin: auto;
    }

    .modal-is-visible .event-form_container {
        /* vertical center the event form div */
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
    }
}

/* font family for Schedule, not the same as TopBar. */

body {
    font-family: "Source Sans Pro", sans-serif;
}

/* -------------------------------- 

Main Components 

-------------------------------- */

.cd-schedule {
    position: relative;
    margin: 2em 0;
}

/* todo: get rid of this */
.cd-schedule::before {
    /* never visible - this is used in js to check the current MQ */
    content: "mobile";
    display: none;
}

.events {
    position: relative;
    z-index: 1;
}

@media only screen and (min-width: 800px) {
    .cd-schedule {
        width: 90%;
        max-width: 1400px;
        margin: 2em auto;
    }
    .cd-schedule::after {
        clear: both;
        content: "";
        display: block;
    }
    .cd-schedule::before {
        content: "desktop";
    }

    .events {
        float: left;
        width: 100%;
    }
}

/** must be below media query which min-width is 800px, or this will not be overridden! */
@media only screen and (min-width: 1000px) {
    .events {
        /* 60px is the .timeline element width */
        width: calc(100% - 60px);
        margin-left: 60px;
    }
}

.progress-container {
    padding-top: 3em;
    position: absolute;
    width: 100%;
    height: 100%;
    text-align: center;
    font-size: 1.3em;
    z-index: 3;
    background-color: rgba(255, 255, 255, 1);
}

.bottom-info {
    padding: 1.2em 5%;
    box-sizing: border-box;
    width: 100%;
}

.github-logo {
    width: 32px;
    height: 32px;
}

.App {
    text-align: center;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
}

.App-header {
    background-color: #0b5761;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

